









































import {
  Component, Ref, Vue, Watch,
} from 'vue-property-decorator';
import { Action, State } from 'vuex-class';
import {
  IInputInfo, IKind, IPage, IContent,
} from '@/types/interface';
import { IContentOrigin, IKindOrigin, IPageOrigin } from '@/store/types';
import * as commonTypes from '@/types/common';
import BaseLabelList from '../../components/BaseLabelList.vue';
import AdvertisingBannerList from '../../components/AdvertisingBannerList.vue';
import InputDropdown from '../../components/InputDropdown.vue';
import InputFormPanel from '../../components/InputFormPanel.vue';

@Component({
  components: {
    BaseLabelList,
    AdvertisingBannerList,
    InputDropdown,
    InputFormPanel,
  },
})

export default class InformationPage extends Vue {
  @Ref('inputForm') readonly inputForm!: InputFormPanel;
  @Action('getKindMenu', { namespace: 'testKind' }) getKindMenu!: (type: string) => Promise<IKindOrigin[]>;
  @Action('getTypePageList', { namespace: 'testKind' }) getTypePageList!: (type: string) => Promise<IPageOrigin[]>;
  @Action('getTypePage', { namespace: 'testKind' }) getTypePage!:
    (params: {
      publicname: string;
      typename: string;
      pagename: string;
    }) => Promise<IContentOrigin[]>;

  kindMenu: IKind[] = [];
  pageList: IPage[] = [];
  contentList: IContent[] = [];
  advertisingList = [
    {
      id: '1',
      imgUrl: '',
    },
    {
      id: '2',
      imgUrl: '',
    },
    {
      id: '3',
      imgUrl: '',
    },
    {
      id: '4',
      imgUrl: '',
    },
  ];

  selectTheme = '';

  isExpand = true;

  get kindList() {
    return this.kindMenu.map((d: IKind) => d.title);
  }

  inputList = [commonTypes.INPUT_NAME, commonTypes.INPUT_PHONE, commonTypes.INPUT_EMAIL,
    commonTypes.INPUT_FULL_ADDRESS, commonTypes.INPUT_CK_CLASS, commonTypes.INPUT_CK_CONSULT];

  created() {
    // const typename = this.$route.query.typename !== null ? this.$route.query.typename : '';
    this.getKindMenu(JSON.parse(JSON.stringify(this.$route.query.typename)))
      .then((data: IKindOrigin[]) => {
        this.kindMenu = data.map((d: IKindOrigin) => ({
          kindname: d.Kindname,
          title: d.Title,
        }));
      });
    this.getTypePageList(JSON.parse(JSON.stringify(this.$route.query.typename)))
      .then((data: IPageOrigin[]) => {
        this.pageList = data.map((d: IPageOrigin) => ({
          pagename: d.Pagename,
          title: d.Title,
        }));
        this.selectTheme = this.pageList[0].pagename;
      });
  }

  @Watch('selectTheme')
  onThemeChange(nVal: string) {
    this.contentList = [];
    this.getTypePage(
      {
        publicname: 'jobs',
        typename: JSON.parse(JSON.stringify(this.$route.query.typename)),
        pagename: nVal,
      },
    )
    .then((data: IContentOrigin[]) => {
      this.contentList = data.map((d: IContentOrigin) => ({
        testType: d.TestType,
        title: d.Title,
        note: d.note,
        actId: d.Act_id,
        pageType: d.PageType,
      }));
    });
  }

  send() {
    const info: IInputInfo | undefined = this.inputForm.getInput();
    console.log(info);
  }
}
