










import {
  Component, Model, Vue, Prop,
} from 'vue-property-decorator';

@Component
export default class BaseLabelList extends Vue {
  @Model('change', { default: () => [] }) readonly list!: string[];
  @Prop({ default: false }) edit!: boolean;

  remove(item: string) {
    const temp = JSON.parse(JSON.stringify(this.list));
    const idx = temp.findIndex((d: string) => d === item);
    temp.splice(idx, 1);
    this.$emit('change', temp);
  }
}
