


















import {
  Component, Model, Prop, Vue,
} from 'vue-property-decorator';
import { IPage } from '@/types/interface';

@Component
export default class InputDropdown extends Vue {
  @Prop({ default: () => [] }) list!: IPage[];
  @Model('change') readonly selected!: string;

  isExpand = true;

  selectHandler(opt: IPage) {
    this.isExpand = true;
    this.$emit('change', opt.pagename);
  }

  get filterList() {
    return this.list.filter((d: IPage) => d.pagename !== this.selected);
  }

  get showText() {
    const idx = this.list.findIndex((d: IPage) => d.pagename === this.selected);
    return idx >= 0 ? this.list[idx].title : '';
  }
}
